    //----------------------------
    // Список bis-menu
    function OutputBisMenuList() {

        const [outputBisMenuList, set_outputBisMenuList] = useState([]);
        const filtersAndSortSettings_FromRedux = useSelector(mState => mState.reduser_filtersAndSortSettings);

        useEffect(() => {
            BisGlobFunctions.globCorpIndication_outputInRedux();
        }, []
        );

        useLayoutEffect(() => {
            refreshBisMenuList();
        }, [filtersAndSortSettings_FromRedux]
        );


        useEffect(() => {  // Скроллинг списка до ранее сохраненной позиции
            // console.log("=== запускаем функцию автоскроллинга");
            if (ref_projectsMenuContenner.current) {
                // предварительно создаем переполнение ВЛОЖЕННОГО блока, увеличиваем высоту вложенного блока до предыдущего сохраненноно размера.   ВАЖНО - это делаем потому, что во время рендеринга Реакт не успевает определить размер вложенного динамического блока и отключает переполнение и скроллинг
                ref_promezhutochnContennerForOutputBisMenuList.current.style.minHeight = last_offsetHeight_OutputBisMenuList + "px";

                // пролистываем список проектов до сохраненной ранее позиции
                ref_projectsMenuContenner.current.scrollTop = scrollPosition_projectsMenuContenner;

                // далее ОБЯЗАТЕЛЬНО с задержкой
                setTimeout(() => {
                    // корректируем размер блока меню под размер фактического размера контента
                    if (ref_OutputBisMenuList.current) {
                        // сначала перезаписываем размер
                        last_offsetHeight_OutputBisMenuList = ref_OutputBisMenuList.current.offsetHeight;
                        // а также корректируем фактический размер блока
                        ref_promezhutochnContennerForOutputBisMenuList.current.style.minHeight = last_offsetHeight_OutputBisMenuList + "px";
                    }
                    // также проверяем переполнение блока для установки дистанции возле скролл
                    GLOB_DATA.checkOverflowMenuList();
                }, 5);
            }
        });


        function refreshBisMenuList() {
            let bisMenuList = [];
            if (
                (bisDataRedux) && (bisDataRedux.projects) && (bisDataRedux.projects.length > 0)
                && corpAccount_Redux
            ) {
                bisMenuList = bisDataRedux.projects.filter(item => (
                    item.parentCorpAccount_ID === corpAccount_Redux.corpAccount_ID
                    &&
                    item.defaultAdminForThisProject === corpAccount_Redux.defaultAdminForThisProject
                ));

                // console.log(bisMenuList);

                // тут реализуем фильтровку и сортировку проектов согласно пользовательского фильтра

                // Фильтр "Не завершенные проекты"
                if (filtersAndSortSettings_FromRedux.projectsFilters[corpAccount_Redux.corpAccount_ID] == "noComplete") {
                    bisMenuList = bisMenuList.filter(item_project => (
                        Number(item_project.projectSettings.status) != 100
                    ));
                }

                // Фильтр "Завершенные проекты"
                if (filtersAndSortSettings_FromRedux.projectsFilters[corpAccount_Redux.corpAccount_ID] == "complete") {
                    bisMenuList = bisMenuList.filter(item_project => (
                        Number(item_project.projectSettings.status) == 100
                    ));
                }



                // далее сортируем согласно пользовательских настроек
                // по умолчанию (по дате - сначала старые)
                if (!filtersAndSortSettings_FromRedux.projectsSorts[corpAccount_Redux.corpAccount_ID]) {
                    bisMenuList.sort((a, b) => a.dateOfCreateProject - b.dateOfCreateProject)
                }

                // сначала новые
                if (filtersAndSortSettings_FromRedux.projectsSorts[corpAccount_Redux.corpAccount_ID] == "beginNew") {
                    bisMenuList.sort((a, b) => b.dateOfCreateProject - a.dateOfCreateProject)
                }

                // по названию  A-Z
                if (filtersAndSortSettings_FromRedux.projectsSorts[corpAccount_Redux.corpAccount_ID] == "byName___A_Z") {
                    bisMenuList.sort((a, b) => String(a.projectSettings.project_Name).localeCompare(String(b.projectSettings.project_Name), undefined, { numeric: true }));
                }

                // по названию  Z-A
                if (filtersAndSortSettings_FromRedux.projectsSorts[corpAccount_Redux.corpAccount_ID] == "byName___Z_A") {
                    bisMenuList.sort((a, b) => String(b.projectSettings.project_Name).localeCompare(String(a.projectSettings.project_Name), undefined, { numeric: true }));
                }

                // по дедлайну
                if (filtersAndSortSettings_FromRedux.projectsSorts[corpAccount_Redux.corpAccount_ID] == "byDedline") {

                    bisMenuList.sort((a, b) => {
                        const parseDeadline = (bisMenuList) => {
                            const { deadline_Date, deadline_Time } = bisMenuList?.projectSettings?.deadline || {};
                            if (!deadline_Date) return Infinity; // Если даты нет → ставим максимально большое значение (пойдет в конец списка)

                            const dateTimeString = deadline_Time ? `${deadline_Date}T${deadline_Time}:00Z` : `${deadline_Date}T00:00:00Z`;
                            return Date.parse(dateTimeString); // Преобразуем в миллисекунды UTC
                        };

                        // console.log("parseDeadline= " + parseDeadline(a));

                        return parseDeadline(a) - parseDeadline(b);
                    });
                }

                // console.log("bisMenuList=");
                // console.log(bisMenuList);

                bisMenuList = bisMenuList.map(
                    (item) => (
                        <BisListMenuSingleContenner
                            key={item.project_ID}
                            project_ID={item.project_ID}
                            project_name={item.projectSettings.project_Name}
                            // посколку мы проходим методом MAP по уже отфильтрованному массиву - нам нужно найти индекс проекта в общей БД, а не в отфильтрованной
                            project_INDEX_inGlobData={BisGlobFunctions.find_Project_index_in_BD(item.project_ID)}
                            activeStatus={(active_Project_ID_FromRedux === item.project_ID) ? true : false}
                            item_by_Props={item}
                            corpAccount_ID={item.parentCorpAccount_ID}
                            m_delClick={() => {
                                my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                                my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialigFormIs_confirmDeleteOneProject" });
                            }}
                        />
                    )
                );

            }
            set_outputBisMenuList(bisMenuList);
        }

        return (
            <div className='OutputBisMenuList'
                ref={ref_OutputBisMenuList}
                style={{
                    display: "flex",
                    flexDirection: "column",
                    flexWrap: "nowrap",
                    width: "100%", // ВАЖНО, иначе выдазит вправо за блок
                    height: "fit-content",
                    height: needUpdateHeigtProjectList ? "fit-content" : "fit-content", // тут принудительно обновляем высоту корп листа
                    // minHeight: "fit-content",

                    paddingRight: isProgectsBlockOverflow ? "6px" : 0, // вслуч переполнение добавляем дистанцию между контентом и полосой прокрутки

                    //  border: "solid 3px red",
                }}
            >
                {outputBisMenuList}

            </div>
        )
    }

