

import React from 'react';
import { useEffect } from 'react';
import { useSelector } from "react-redux";
import './globStyles.css';
import './color_Teams.css';

import { DialogForm_Fone_LEVEL1, DialogForm_Fone_LEVEL2, DialogForm_Fone_LoaderGlob } from './KontentBlock/dialogForm.jsx';
import HeaderBlock from "./HeaderBlock/HeaderBlock.jsx";
import KontentBlock from "./KontentBlock/KontentBlock.jsx";


function App() {

  // Один раз при загрузке страницы.
  useEffect(() => {

  }, []
  ) // запускаем один раз при создании


  const isDialogVisible = (useSelector(mState => mState.reduser_DialogForm.isDialogVisible));
  const carrentActiveSubdialog = (useSelector(mState => mState.reduser_DialogForm.carrentActiveSubdialog));
  const carrentActiveSubdialog_2level = (useSelector(mState => mState.reduser_DialogForm.carrentActiveSubdialog_2level));
  const carrentActiveSubdialog_5level = (useSelector(mState => mState.reduser_DialogForm.carrentActiveSubdialog_5level));

  return (
    <div className='AppBlock'
      style={{
        position: "fixed",
        top: 0,
        right: 0,
        left: 0,
        bottom: 0,
        display: 'flex',
        flexDirection: 'column',
        margin: 0,
        padding: 0,
        paddingLeft: "4px",
        paddingRight: "4px",
        border: "none",
      }}
    >

      <HeaderBlock />
      <KontentBlock />

      {/* Это диалоговые окна, по умолчанию скрыто  */}
      {isDialogVisible ? <DialogForm_Fone_LEVEL1 /> : null}
      {carrentActiveSubdialog_2level ? <DialogForm_Fone_LEVEL2 /> : null}
      {carrentActiveSubdialog_5level ? <DialogForm_Fone_LoaderGlob /> : null}

    </div>
  );
}

export default App;




