// вывод чата Активного проекта
function ChatList(props) {
    const chatListRedux = useSelector(mState => mState.reduser_chatListRedux);
    const languageChoosed_Redux = useSelector(mState => mState.reduser_languageChoosed);

    const [isEndViewChat, set_isEndViewChat] = useState(true);
    const [indicationNnewMessages, set_indicationNewMessages] = useState(false);
    const [isAnyUnloadedPreviousMessages, set_isAnyUnloadedPreviousMessages] = useState(false);
    const [finedIndexInChatListRedux, set_finedIndexInChatListRedux] = useState(-1);
    const ref_chatList = useRef();

    useEffect(() => {
        // при переходе на другой проект устанавливаем нижнюю привязку списка
        set_isEndViewChat(true);
    }, [props.project_OR_subProject___id]
    )

    useEffect(() => { // при обновлении chatListRedux - определяем и устанавливаем текущий индекс чата, а также наличие незагруженных предыдущих сообщений
        if (!props.project_OR_subProject___id) {
            set_finedIndexInChatListRedux(-1);
            return;
        }

        // console.log("isEndViewChat= " + isEndViewChat);

        // определяем текущий индекс чата в локальной БД
        let current_chatIndex_inChat_BD = BisGlobFunctions.find_chatIndex_inChat_BD(props.project_OR_subProject___id);

        // выявляем наличие незагруженных предыдущих сообщений
        let necessityUnloadMessages = BisGlobFunctions.needsUploadPreviousChatMessages_CurrentProject(props.project_OR_subProject___id);

        // при необходимости подгружаем предыдущие сообщения - когда при первой загрузке просматривается верхний край чата, и при этом имеются незагруженные предыдущие сообщения
        if ((ref_chatList.current.scrollTop < 20) && (necessityUnloadMessages === true)) {
            BisGlobFunctions.upload_PreviousMessages_CurrentProject(
                props.project_OR_subProject___id,
                chatListRedux[current_chatIndex_inChat_BD].messages[0].кnownIndexInBD,
                10   // количество сообщений для подгрузки
            )
        }

        if (current_chatIndex_inChat_BD != null && current_chatIndex_inChat_BD >= 0) {
            // передаем текущий индекс чата в состояние
            set_finedIndexInChatListRedux(current_chatIndex_inChat_BD);
            // передаем наличие/отсутствие незагруженных предыдущих сообщений в состояние
            set_isAnyUnloadedPreviousMessages(necessityUnloadMessages);
        }

        // СКРОЛЛИНГ В КОНЕЦ СПИСКА -  при каждом изменении, проверяем наличие нижней привязки и при необходимости прокручиваем список вниз
        if (isEndViewChat === true) {
            // определяем высоту списка
            let scrollingValue = ref_chatList.current.scrollHeight;
            // прокручиваем список до конца вниз
            if (scrollingValue) ref_chatList.current.scrollTop = scrollingValue;

            // В ЗАВИСИМОСТИ ОТ ПРИНАДЛАЖНОЕСТИ ЛИБО К ЧАТУ, ЛИБО К СУБ-ЧАТУ - проверяем необходимость обновления времени просмотра активного чата
            // если это чат для суб-проекта
            if (props.parent_Project_ID) {
                let finedProjectIndex = BisGlobFunctions.find_Project_index_in_BD(props.parent_Project_ID);
                let fined_subProjectIndex = BisGlobFunctions.find_subProject_index_in_BD(finedProjectIndex, props.project_OR_subProject___id);
                BisGlobFunctions.check_needUpdate_Time_WasRead_subChat(finedProjectIndex, fined_subProjectIndex);
            }
            // иначе если это чат верхнего проекта
            else {
                BisGlobFunctions.check_needUpdate_Time_WasReadChat(
                    BisGlobFunctions.find_Project_index_in_BD(props.project_OR_subProject___id)
                )
            }
        }

        else {
            // Проверяем и устанавливаем индикацию нов сообщений
            // В ЗАВИСИМОСТИ ОТ ПРИНАДЛАЖНОЕСТИ ЛИБО К ЧАТУ, ЛИБО К СУБ-ЧАТУ - проверяем необходимость обновления времени просмотра активного чата
            // если это чат для суб-проекта
            if (props.parent_Project_ID) {
                // УСТАНОВИТЬ ФУНКЦИЮ ДЛЯ СУБЧАТА
            }

            // иначе если это чат верхнего проекта
            else {
                set_indicationNewMessages(
                    BisGlobFunctions.need_Indication_newMessage_chat_current_mainProject(
                        BisGlobFunctions.find_Project_index_in_BD(props.project_OR_subProject___id)
                    )
                )
            }
        }
    },
    )

    return (
        <div className='ChatList'
            style={{
                display: "flex",
                flexDirection: "column",
                height: "100%", // Нужно, иначе при пустом контекте нижние блоки смещаются вверх
                width: "100%",
                overflow: "hidden",
                overflowY: "auto",
                paddingRight: "4px",
                // flex: 1,
            }}
            id={props.chatType} // в качестве id через пропс передаем либо "id_projectChat" либо  "id_subProjectChat"
            ref={ref_chatList}

            onScroll={() => {
                let bottomScroll =
                    ref_chatList.current.scrollHeight // полная высота элемента, включая содержимое, невидимое из-за прокрутки
                    - ref_chatList.current.scrollTop // дистанция ​​от верхa всего блока до верхней границы текущего элемента
                    - ref_chatList.current.offsetHeight; // видимая высота контента, с учётом вертикальных полей и границ

                // console.log("deltaScroll= " + deltaScroll);

                if (bottomScroll > 4) set_isEndViewChat(false);
                else set_isEndViewChat(true);

                // проверяем необходимость подгрузки сообщений с сервера
                if ((ref_chatList.current.scrollTop < 20)
                    && (isAnyUnloadedPreviousMessages === true)) {
                    BisGlobFunctions.upload_PreviousMessages_CurrentProject(
                        props.project_OR_subProject___id,

                        (finedIndexInChatListRedux != null && finedIndexInChatListRedux >= 0)
                            ? chatListRedux[finedIndexInChatListRedux].messages[0].кnownIndexInBD
                            : null,
                        2
                    )
                }
            }}
        >

            {/*     Если еще не все сообщения загружены - отображаем Лоадер    */}
            {
                isAnyUnloadedPreviousMessages === true
                    ?
                    <div className="loadingIndicatorBlock" >
                        === loading ...
                    </div>
                    : ""
            }

            { // Это блок с сообщениями
                (finedIndexInChatListRedux != null && finedIndexInChatListRedux >= 0)
                    ?
                    chatListRedux[finedIndexInChatListRedux].messages.map(
                        (item, /* index */) => (
                            <div
                                className="singleArrayBlock_MainChat"
                                key={BisGlobFunctions.myRandomId()}
                            >

                                < SingleBlock_Chat
                                    autor={item.autor}
                                    user_Email={item.autor}
                                    messDate={item.timeOfCreate}
                                    messText={item.textMessage}
                                />
                            </div>
                        )
                    )
                    : null
            }


            <div className="vectorToDown_contenner"

                style={{
                    cursor: "pointer",
                    position: "absolute",
                    display: isEndViewChat ? "none" : "flex",
                    bottom: "62px",
                    right: "16px",
                    alignItems: "center",
                    padding: "10px",
                }}

                onClick={() => {
                    // определяем высоту списка
                    let scrollingValue = ref_chatList.current.scrollHeight;
                    // прокручиваем список до конца вниз
                    if (scrollingValue) ref_chatList.current.scrollTop = scrollingValue;
                    // отмечаем отсутсвие непрочитанных сообщений
                    set_isEndViewChat(true);
                }}
            >

                {indicationNnewMessages === true
                    ?
                    <div className="indicationBlock"
                        style={{
                            height: "16px",
                            marginRight: "6px",
                        }}
                    >
                        <div className="svg__ProjectIndication"

                            title={
                                //Языковой блок
                                (languageChoosed_Redux == "de" ? "Neue Nachrichten" : "") +
                                (languageChoosed_Redux == "pl" ? "Nowe wiadomości" : "") +
                                (languageChoosed_Redux == "ua" ? "Нові повідомлення" : "") +
                                (languageChoosed_Redux == "ru" ? "Новые сообщения" : "") +
                                (languageChoosed_Redux == "en" ? "New messages" : "")
                            }
                        >
                            <m_SVG_ICONS.message_Project className="mSvgFile" />
                        </div>
                    </div>
                    : ""
                }

                <div className="vectorBlock"
                    style={{
                        height: "24px",
                        width: "24px",
                        justifyContent: "center",
                        alignItems: "center",
                        border: "solid 1px",
                        borderRadius: "50%",
                    }}
                >
                    <div className="svg__btnSymbol">
                        <m_SVG_ICONS.vectorDown className="mSvgFile" />
                    </div>
                </div>
            </div>

        </div>
    )

}

//----------------------------
// блок чата вместе с историей сообщений, полем ввода и кнопкой для отправки
function ChatBlock(props) {

    const bisDataRedux = useSelector(mState => mState.reduser_bisData);
    const languageChoosed_Redux = useSelector(mState => mState.reduser_languageChoosed);
    const active_project_INDEX_FromRedux = useSelector(mState => mState.reduser_ActiveProjectAndSubproject.currentActiveProject_INDEX);
    const active_subProject_INDEX_FromRedux = useSelector(mState => mState.reduser_ActiveProjectAndSubproject.currentActiveSubProject_INDEX);
    const ref_inputFieldChat = useRef();
    const currentUser_Redux = useSelector(mState => mState.reduser_currentUser);
    const active_Project_ID_FromRedux = useSelector(mState => mState.reduser_ActiveProjectAndSubproject.currentActiveProject_ID);

    // при изменении текущего чата очищаем поле ввода чата
    useEffect(() => {
        ref_inputFieldChat.current.value = ""; // очищаем текст в поле ввода
    }, [props.project_OR_subProject___id]
    )

    return (
        <div className="ChatBlock"
            style={{
                position: "relative",
                display: "flex",
                flexDirection: "column",
                width: "100%",
                height: "100%",
            }}
        >

            <div className="chatListContenner"
                style={{
                    flexDirection: "column",
                    borderRadius: "4px",
                    // height: "100%",
                    width: "100%",
                    borderRadius: "4px",
                    overflow: "hidden",
                    flex: 1,
                }}
            >



                <ChatList
                    chatType={props.chatType}
                    parent_Project_ID={props.parent_Project_ID} // этот имеет значение для чатов субпроектов
                    project_OR_subProject___id={props.project_OR_subProject___id}
                />

            </div>


            <form className="blockSendMessage"
                style={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "space-between",
                    alignItems: "center",
                    width: "100%",
                    marginTop: "10px",
                }}
                action="submit"
                onSubmit={(e) => {
                    e.stopPropagation(); // предотвращение всплытия события вверх
                    e.preventDefault(); // предотвращение деяствия по умолчанию для элемента HTML, в т.ч. предотвращение отправки формы и последующей перезагрузки окна после отправки формы

                    // console.log(" ");
                    // console.log("---onSubmit= ");
                    // console.log();

                    if (!ref_inputFieldChat.current.value) return;

                    // если это сообщения чата проекта

                    if (active_project_INDEX_FromRedux >= 0) {

                        // console.log("props=");
                        // console.log(props);

                        BisGlobFunctions.sendChatProjectMessage(

                            props.project_OR_subProject___id,
                            currentUser_Redux,
                            ref_inputFieldChat.current.value,
                            // исправить - убрать проверку через тернарный оператор после упорядочивания структуры данных на сервере
                            bisDataRedux.projects[active_project_INDEX_FromRedux].chat_knownIndexInReestr
                                ? bisDataRedux.projects[active_project_INDEX_FromRedux].chat_knownIndexInReestr
                                : null
                        );
                        ref_inputFieldChat.current.value = ""; // очищаем текст в поле ввода

                        // при отправке сообщение прокручиваем спискок вниз, чтобы не висело свое непрочитанное сообщение
                        // исправить - помещать отправленное сообщение в чат только после подтверждения сервером
                        let element = document.getElementById("id_projectChat"); // нужно, чтобы найти и прокрутить список вниз при отправке сообщения
                        if (element) element.scrollTop = element.scrollHeight;
                    }


                    // если это сообщения чата субпроекта
                    if (props.chatType == "id_subProjectChat---") {

                        console.log("props=");
                        console.log(props);

                        if (active_subProject_INDEX_FromRedux >= 0) {
                            BisGlobFunctions.sendChat_subProjectMessage(
                                active_Project_ID_FromRedux,
                                active_subProject_INDEX_FromRedux,
                                currentUser_Redux,
                                ref_inputFieldChat.current.value,
                                null,  // это известный индекс чата в БД на сервере, передавать сюда через пропсы
                            );
                            ref_inputFieldChat.current.value = ""; // очищаем текст в поле ввода

                            // при отправке сообщение прокручиваем спискок вниз, чтобы не висело свое непрочитанное сообщение
                            let element = document.getElementById("id_subProjectChat"); // нужно, чтобы найти и прокрутить список вниз при отправке сообщения
                            if (element) element.scrollTop = element.scrollHeight;
                        }
                    }
                }}
            >

                <textarea className="inputFieldChatList"
                    style={{
                        cursor: "text",
                        flex: 1,
                        resize: "none",
                        outline: "none",
                        border: "none",
                        borderRadius: "4px",
                        height: "30px",
                        minHeight: "3em",
                    }}

                    type="text"
                    ref={ref_inputFieldChat}
                    disabled={(active_Project_ID_FromRedux) ? false : true}
                    // maxlength="20"

                    name="message" //  ! этот атрибут указывает, что форма по умолчанию отправляет сообщение с этого поля 
                    id=""
                    // cols="30"
                    // rows="3"

                    onKeyDown={(e) => {
                        // console.log("e= ");
                        // console.log(e.key);
                        if (e.key == "Enter" && !e.shiftKey) {
                            e.preventDefault(); // предотвращаем перенос строки, чтобы активировать нажатие на кнопку submit

                            // далее инициируем событие submit родительской формы
                            e.target.form.dispatchEvent(new Event(
                                "submit", // это тип события, которое пытаемся вызвать
                                {
                                    cancelable: true, // этот атрибут указывает, можно ли отменить событие с помощью event.preventDefault()
                                    bubbles: true // разрешение/запрет всплытия события
                                }
                            ));
                        }
                    }}
                >
                </textarea>





                <button className={
                    (active_Project_ID_FromRedux)
                        ? "mBtn_onlyIcons_01"
                        : "mBtn_onlyIcons_01 btnDisabled"
                }

                    style={{
                        marginLeft: "10px",
                    }}

                    tabIndex={-1} // чтобы при неактивном классе btnDisabled было невозможно перейти на кнопку через клавишу TAB
                    type='submit'
                >

                    <div className="svg__btnSymbol greenColorStyle" >
                        <m_SVG_ICONS.sendMessage className="mSvgFile" />
                    </div>
                    {/*    Языковой блок    */}
                    {/* 
                                {(languageChoosed_Redux == "de") ? "Schicken" : null}
                                {(languageChoosed_Redux == "pl") ? "Wysłać" : null}
                                {(languageChoosed_Redux == "ua") ? "Відправити" : null}
                                {(languageChoosed_Redux == "ru") ? "Отправить" : null}
                                {(languageChoosed_Redux == "en") ? "Send" : null}
                                */}
                </button >
            </form>

        </div>
    )



}

