// в пропсах передавать: адрес картинки, имя юзера, Емейл юзера
function SingleBlock_Chat(props) {
    const usersOnlineStatusRedux = useSelector(mState => mState.reduser_usersOnlineStatus);
    const currentUser_Redux = useSelector(mState => mState.reduser_currentUser);
    const [isOnlineCurrentUser, set_isOnlineCurrentUser] = useState(false);

    let adapting_Time = new Date(props.messDate).toLocaleTimeString();
    let adapting_Date = "(" + (new Date(props.messDate).toLocaleDateString()) + ")";

    useEffect(() => {
        let onlineStatus = BisGlobFunctions.getOnlineStatusCurrentUser(props.user_Email);
        set_isOnlineCurrentUser(onlineStatus);
    }, [usersOnlineStatusRedux]
    )

    // console.log("props=");
    // console.log(props);
    // console.log("serverPort=");
    // console.log(mConfig.serverPort + "/static/avatars/av___" + props.user_Email + ".jpeg");

    return (
        <div
            className="singleChatBlock"
            className={props.user_Email == currentUser_Redux
                ? "singleChatBlock myMessageInChat"
                : "singleChatBlock"}

            style={{
                display: "flex",
                flexDirection: "row",
                flexWrap: "nowrap",
                padding: "2px",
                marginTop: "6px",
                // для свох сообщений добавляем стили, методом разворачивания свойств дополнительного объекта
                ...props.user_Email == currentUser_Redux
                    ?
                    {
                        // border: "1px solid",
                        // border: "4px solid red",
                        borderRadius: "5px",
                        width: "100%",
                        marginLeft: "40px",
                    }
                    : {}
                ,
            }}
        >

            <div className="singleChatBlock_left"
                style={{
                    alignItems: "flex-start",
                }}
            >
                <UserAvatarBlock
                    // imgAvatarAdress="https://i.pinimg.com/236x/8a/de/fe/8adefe5af862b4f9cec286c6ee4722cb.jpg" 
                    userName={props.autor}
                    user_Email={props.autor}
                    isOnlineCurrentUser={isOnlineCurrentUser}
                    avatarSize_PX="30px"
                />
            </div>

            <div className="singleChatBlock_right"
                style={{
                    flexDirection: "column",
                    marginLeft: "4px"
                }}
            >

                <div className="singleChatBlock_right_TOP">
                    <div className="timeDateBlock smallTextKontent_type02">
                        <div className="timeBlock">
                            {adapting_Time + " " + adapting_Date}
                        </div>
                    </div>
                </div>


                {/* 
                <div className="singleChatBlock_right_BOTTOM"
                    style={{
                        fontSize: "1.0em",
                    }}
                >
                    {props.messText}
                </div>
                */}


                <div className="singleChatBlock_right_BOTTOM"
                    style={{
                        cursor: "default",
                        fontSize: "1.0em",
                        width: "100%",
                        height: "fit-content",
                        //maxHeight: "fit-content",

                        resize: "none",
                        outline: "none",
                        border: "none",
                        background: "none",
                        color: "inherit",
                        fontSize: "1.0em",
                        fontStyle: "italic",

                        whiteSpace: 'pre-wrap', // учитывать переносы строк (\n) в тексте
                    }}
                    readOnly
                >
                    {props.messText}
                </div>







            </div>

        </div >
    )
}

