function TeamList_activeProject() {
    const bisDataRedux = useSelector(mState => mState.reduser_bisData);

    const active_project_INDEX_FromRedux = useSelector(mState => mState.reduser_ActiveProjectAndSubproject.currentActiveProject_INDEX);

    const usersOnlineStatusRedux = useSelector(mState => mState.reduser_usersOnlineStatus);

    const [isOnlineCurrentUser, set_isOnlineCurrentUser] = useState(false);

    let teamList = "";

    if (active_project_INDEX_FromRedux >= 0) {

        teamList = bisDataRedux.projects[active_project_INDEX_FromRedux].teamList.map(
            (item, /* index */) => (
                <div className="TeamList_activeProject"
                    key={item.user_Email}
                >
                    <UserAvatarBlock
                        userName={item.user_Email} // установить имя
                        user_Email={item.user_Email}
                        isOnlineCurrentUser={isOnlineCurrentUser} // установить фактический онлайн статус
                        avatarSize_PX="30px"

                    />
                </div>
            )
        )
    }
    return (
        <div className="TeamList_activeProject"
            style={{
                wight: "100%",
                height: "100%",
                overflow: "hidden",
                overflowX: "auto",
                paddingBottom: "2px",
            }}
            onWheel={(e) => {
                e.preventDefault(); // Чтобы предотвратить вертикальную прокрутку страницы
                // тут вращение колесика мыши преобразуем в горизонтальный скролл. Хотя тут это не нужно, поскольку страница виксирована
                e.stopPropagation();
                e.currentTarget.scrollLeft += e.deltaY;
            }}
        >
            {teamList}
        </div>
    )
}

//----------------------------

function TeamList_activeSubProject() {
    const bisDataRedux = useSelector(mState => mState.reduser_bisData);

    const active_project_INDEX_FromRedux = useSelector(mState => mState.reduser_ActiveProjectAndSubproject.currentActiveProject_INDEX);

    const active_subProject_INDEX_FromRedux = useSelector(mState => mState.reduser_ActiveProjectAndSubproject.currentActiveSubProject_INDEX);

    const [isGorizontalOverflow, set_isGorizontalOverflow] = useState(false);
    const ref_teamCBlock = useRef();

    useEffect(() => {  // тут отслеживаем переполнение блока
        if (ref_teamCBlock.current) BisGlobFunctions.checkAndSet_gorizontalOverflowBlock(ref_teamCBlock, isGorizontalOverflow, set_isGorizontalOverflow);
    }, [])


    let teamList = null;

    if (
        (active_project_INDEX_FromRedux >= 0)
        && (active_subProject_INDEX_FromRedux >= 0)
        && (bisDataRedux.projects[active_project_INDEX_FromRedux]?.subProjects[active_subProject_INDEX_FromRedux]?.teamList_ofResponsible_subProject)
    ) {
        teamList = bisDataRedux.projects[active_project_INDEX_FromRedux].subProjects[active_subProject_INDEX_FromRedux].teamList_ofResponsible_subProject.map(
            (item, /* index */) => (
                <div className="avatarBlock"
                    key={item.user_Email}
                >
                    <UserAvatarBlock
                        userName={item.user_Email} // установить имя
                        user_Email={item.user_Email}
                        isOnlineCurrentUser={false} // установить фактический онлайн статус
                        avatarSize_PX="30px"
                    />
                </div>
            )
        )
    }

    return (
        <div className="TeamList_activeSubProject"
            ref={ref_teamCBlock}
            style={{
                wight: "100%",
                // height: "100%",
                overflow: "hidden",
                overflowX: "auto",
                // paddingBottom: isGorizontalOverflow ? "4px" : "0", // НЕ УДАЛЯТЬ
                // background: isGorizontalOverflow ? "yellow" : "blue",
                scrollbarGutter: "stable", // Оставляет место для скроллбара
            }}
            onWheelCapture={(e) => {
                e.preventDefault(); // Чтобы предотвратить вертикальную прокрутку страницы
                // тут вращение колесика мыши преобразуем в горизонтальный скролл. Хотя тут это не нужно, поскольку страница виксирована
                e.currentTarget.scrollLeft += e.deltaY;
            }}
        >
            {teamList}
        </div>
    )
}

//----------------------------

// Для избежания повторных запросов на сервер для получения аватарок пользователей, А ТАКЖЕ для избежания помигивания аватарок при многократных обновлениях - данные изображений храним в глоб переменной
// Для обновления изображений не используем Redux, поскольку: 1) Аватарки на странице многократно рендерятся и каждый раз считывают измененные данные из глоб переменной 2) Обновление картинок происходит автоматически через HTML при получении корректного изображения (или адреса изображения)
// в пропсах передаем: userName, user_Email, isOnlineCurrentUser
function UserAvatarBlock(props) {
    // console.log("=== Запуск UserAvatarBlock");
    // если объекта аватара не существует для данного юзера  - создаем его
    if (props.user_Email && !GLOB_DATA.usersAvatarImages[props.user_Email]) {
        // В создаваемом объекте сразу устанавливаем значение "noAvatarOnServer", впоследствии при положительном ответе с сервера - изменим его содержимое на объект аватара
        GLOB_DATA.usersAvatarImages[props.user_Email] = "noAvatarOnServer";
        // console.log("Запускаем загрузку картинки");
        let newImg = new Image(100, 100);
        newImg.src = mConfig.serverPort + "/avatars/av___" + props.user_Email + ".jpeg";
        newImg.onload = (() => {
            // console.log("Созраняем картинку");
            newImg.onload = null; // удаляем встоенную фун перед передачей картинки
            newImg.onError = null; // удаляем встоенную фун перед передачей картинки
            GLOB_DATA.usersAvatarImages[props.user_Email] = newImg;
        });
        newImg.onError = (() => {
            console.log("Созраняем отсутствие картинки");
            GLOB_DATA.usersAvatarImages[props.user_Email] = "noAvatarOnServer";
        })
    }

    return (
        <div className="UserAvatarBlock"
            title={props.userName}
            style={{
                // cursor: "default",
                display: "flex",
                position: "relative",
                justifyContent: "center",
                alignItems: "center",
                textAlign: "center",
                overflow: "hidden",
                margin: "1px",

                height: props.avatarSize_PX,
                width: props.avatarSize_PX,

                paddingBottom: "2px",
            }}
        >

            <div className="userImageOrNameRadiusBlock"
                style={{
                    cursor: "default",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    textAlign: "center",
                    borderRadius: "50%",
                    overflow: "hidden",
                    height: "100%",
                    width: "100%",
                }}
            >
                {
                    (
                        GLOB_DATA.usersAvatarImages[props.user_Email]
                        && (GLOB_DATA.usersAvatarImages[props.user_Email] != "noAvatarOnServer")
                    )
                        ?
                        <img src={(GLOB_DATA.usersAvatarImages[props.user_Email]).src}
                            height={"100%"} />
                        :
                        <div className="userName"
                            style={{
                                justifyContent: "center",
                                alignItems: "center",
                                textAlign: "center",
                                fontSize: "1.2em",
                                height: "fit-contens",
                            }}
                        >
                            {
                                props.userName
                                    ? props.userName.split("", 1)[0].toUpperCase()
                                    : "NoInfo"
                            }
                        </div>

                }
            </div>

            {
                props.isOnlineCurrentUser ?
                    <div className="userOnlineStatus"
                        style={{
                            position: "absolute",
                            width: "6px",
                            height: "6px",
                            borderRadius: "50%",
                            left: "1px",
                            bottom: "1px",
                            // zIndex: 2,
                        }}
                    >
                    </div>
                    :
                    ""
            }



        </div >
    )

}

