import { GLOB_DATA } from "../GLOB_DATA.js";
import React, { useState, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { psevdoServer, psevdoServer_info } from "../bisFormsAndFunctions/psevdoServer.js";
import BisGlobFunctions from "../bisFormsAndFunctions/bisGlobFunctions.js";
import { loginService, mRefreshToken } from "../bisFormsAndFunctions/loginService.js";
import m_SVG_ICONS from '../m_SVG_ICONS.js';
import mConfig from '../config.js';
import Log from 'jwt-encode';


export function MainMenuButtonDropDown() {
    const [isActiveMenu, set_isActiveMenu] = useState(false);
    const shortMenuType_Redux = useSelector(mState => mState.reduser_shortMenuType);
    const soundOnStatus_Redux = useSelector(mState => mState.reduser_soundOnStatus);
    const currentUser_Redux = useSelector(mState => mState.reduser_currentUser);
    const accessToken_Redux = useSelector(mState => mState.reduser_accessToken);
    const languageChoosed_Redux = useSelector(mState => mState.reduser_languageChoosed);
    const my_dispatch = useDispatch();
    const colorShemeNumber_Redux = useSelector(mState => mState.reduser_colorSheme);
    const mediaAdaptationData_mobilAdaptationName_FromRedux = useSelector(mState => mState.reduser_mediaAdaptionData.mobilAdaptationName);
    const bisDataRedux = useSelector(mState => mState.reduser_bisData);
    const chatListRedux = useSelector(mState => mState.reduser_chatListRedux);

    return (
        //     Это кнопка со вложенным выпадающим меню      
        < div className="MainMenuButtonDropDown"
            // важно - присваиваем блоку nav произвольное значение свойства tabIndex, чтобы он начал поддерживать фокусировку, поскольку по умолчанию элементы типа DIV, NAV и т.п. не поддерживают получение фокуса

            tabIndex={1002}
            style={{
                cursor: "pointer",
                position: "relative",
                padding: 0,
                margin: 0,
                border: "none",
                // display: "flex",
                display: shortMenuType_Redux ? "flex" : "none",
                alignItems: "center",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "row",
                flexWrap: "nowrap",
                whiteSpace: "nowrap",
                outline: "none", // необх, чтобы в мобильном приложении не было вокруг рамки при активации

                marginLeft: "10px",
                marginRight: "10px",
                paddingLeft: "8px",
                paddingRight: "8px",
                borderRadius: "2px",
                transition: "0.2s",
            }}

            // Важно - тут используем onClick , а не onClickCapture, иначе событий перехватывается на родительском элементе, что приводит к закрыванию меню
            onClick={() => {
                // при повторных кликах открываем/закрываем меню
                set_isActiveMenu(!isActiveMenu);
            }}

            onBlur={() => set_isActiveMenu(false)}
            onMouseLeave={() => set_isActiveMenu(false)}
        >

            <div className="menuBlock_Struct_Ctnter"
                style={{
                    display: "block",
                    // height: "16px"
                }}
            >
                Menu
            </div>

            <div className="menuBlock_Struct_Rigt"
                style={{
                    display: "block",
                    paddingLeft: "5px", // дистанция м-у маркером и надписью
                    paddingTop: "4px", // опускаем, чтобы выровнять с надписью
                    height: "16px"
                }}
            >
                <div className="svg__btnSymbol">
                    <m_SVG_ICONS.vectorDown className="mSvgFile" />
                </div>
            </div>

            {/*     Это ФОН выпадающего меню      */}
            < div className="fon_downSlider_menu"
                style={{
                    display: isActiveMenu ? 'flex' : 'none',
                    position: "absolute",
                    top: 0,
                    left: "-5px",
                    padding: "5px",
                    paddingTop: "40px",
                    zIndex: 2,
                }}
            >

                {/*     Это КОНТЕНТ выпадающего меню      */}
                < div className="kontent_dropDown_menu"
                    style={{
                        display: "flex",
                        flexDirection: "column",
                        overflow: "hidden",
                        maxWidth: "300px",
                        padding: "10px",
                        // background: "green", // необх, чтобы в мобильном приложении не было вокруг рамки при активации
                        // border: "solid 1px"
                    }}
                    // Важно - тут используем onClick , а не onClickCapture, иначе событий перехватывается на родительском элементе, и в сочетании с командой stopPropagation(), прерывается погружение, и не срабатывают вложенные команды меню
                    onClick={(e) => {
                        // ВАЖНО - предотвращаем всплытие события, чтобы не закрывалось меню
                        e.stopPropagation();
                    }}
                >



                    <div className="contennerForBtn"
                        style={{
                            display: mediaAdaptationData_mobilAdaptationName_FromRedux ? "flex" : "none",
                            justifyContent: "flex-end",
                            width: "100%",
                            marginBottom: "20px",
                        }}
                    >

                        <div className="closeFormBlock"

                            onClickCapture={() => {
                                set_isActiveMenu(false);
                            }}
                        >
                            X {/* заменить на виджет */}
                        </div>
                    </div>










                    {/*     Это кнопка цветовой темы      */}
                    <div className="menuSubContent_Button"
                        style={{
                            justifyContent: "space-between",
                        }}
                        onClickCapture={() => {
                            BisGlobFunctions.changeColorSheme();
                        }}
                    >

                        <div className="left">
                            {/*    Языковой блок    */}
                            {(languageChoosed_Redux == "de") ? "Color theam" : null}
                            {(languageChoosed_Redux == "pl") ? "Color theam" : null}
                            {(languageChoosed_Redux == "ua") ? "Color theam" : null}
                            {(languageChoosed_Redux == "ru") ? "Color theam" : null}
                            {(languageChoosed_Redux == "en") ? "Color theam" : null}
                        </div>


                        <div className="right">
                            <div className="roundBbutton colorsBtn"
                                title="Color theam"
                                style={{
                                    fontSize: "1.2em",
                                    fontWeight: "bold",
                                    marginLeft: "20px",
                                }}
                            >
                                {colorShemeNumber_Redux}
                            </div>
                        </div>

                    </div>

                    {/*     Это кнопка звука     */}
                    <div className="menuSubContent_Button"
                        style={{
                            justifyContent: "space-between",
                        }}
                        onClick={() => {
                            BisGlobFunctions.changeSoundOn();
                        }}
                    >

                        <div className="left">
                            {/*    Языковой блок    */}
                            {(languageChoosed_Redux == "de") ? "Ton ein-/ausschalten" : null}
                            {(languageChoosed_Redux == "pl") ? "Włącz/wyłącz dźwięk" : null}
                            {(languageChoosed_Redux == "ua") ? "Увім/вимкн звук" : null}
                            {(languageChoosed_Redux == "ru") ? "Вкл/выкл звук" : null}
                            {(languageChoosed_Redux == "en") ? "Turn On/Off sound" : null}
                        </div>


                        <div className="right">
                            <div className="roundBbutton"
                                title="Color theam"
                                style={{
                                    fontSize: "1.2em",
                                    fontWeight: "bold",
                                    marginLeft: "20px",
                                }}
                            >
                                <div className="svg__btnSymbol" >
                                    {
                                        (soundOnStatus_Redux == true)
                                            ? <m_SVG_ICONS.signalOn className="mSvgFile" />
                                            : <m_SVG_ICONS.signalOff className="mSvgFile" />
                                    }
                                </div>
                            </div>
                        </div>

                    </div>

                    {/*     Это кнопка контактов     */}
                    <div className="menuSubContent_Button"
                        style={{
                            display: (accessToken_Redux && currentUser_Redux)
                                ? "flex"
                                : "none",
                            justifyContent: "space-between",
                        }}
                        onClickCapture={() => {
                            // Альтернатива disabled - при необходимости прерываем onClick:
                            if (!(accessToken_Redux && currentUser_Redux)) return null;
                            my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                            my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialogFormIs_setContactList" });
                        }}
                    >

                        <div className="left">
                            {/*    Языковой блок    */}
                            {(languageChoosed_Redux == "de") ? "Kontaktliste ..." : null}
                            {(languageChoosed_Redux == "pl") ? "Lista kontaktów ..." : null}
                            {(languageChoosed_Redux == "ua") ? "Список контактів ..." : null}
                            {(languageChoosed_Redux == "ru") ? "Список контактов ..." : null}
                            {(languageChoosed_Redux == "en") ? "Contact list ..." : null}
                        </div>


                        <div className="right">
                            <div className="Contacts_list"
                                title="Color theam"
                                style={{
                                    display: "flex",
                                    flexDirection: "row",
                                    alignItems: "center",
                                    marginLeft: "20px",
                                    marginRight: "6px",
                                    height: "24px",

                                    cursor: 'pointer',
                                }}
                            >
                                <div className="svg__btnSymbol" >
                                    <m_SVG_ICONS.contact_list className="mSvgFile" />
                                </div>
                            </div>
                        </div>

                    </div>


                    {/*    Выбор способа отображения меню   */}
                    <div className="menuContentBlock_TextOnly"
                        style={{
                            flexDirection: "column",
                            marginTop: "14px",
                        }}
                    >

                        <div className="textBlock">
                            {/*    Языковой блок    */}
                            {(languageChoosed_Redux == "de") ? "Menüansicht:" : null}
                            {(languageChoosed_Redux == "pl") ? "Widok menu:" : null}
                            {(languageChoosed_Redux == "ua") ? "Вид меню:" : null}
                            {(languageChoosed_Redux == "ru") ? "Вид vеню:" : null}
                            {(languageChoosed_Redux == "en") ? "Menu view:" : null}
                        </div>

                        <div className="choosingBlock"
                            style={{
                                flexDirection: "column",
                                // justifyContent: "space-between",
                                alignItems: "flex-start",
                            }}
                        >

                            {/*    Это кнопка "Свернуть меню"    */}
                            <div className="menuSubContent_Button"
                                onClickCapture={() => {
                                    BisGlobFunctions.setShortMenuType(true)
                                }}
                            >
                                <div className="check_Block">
                                    <input
                                        type="checkbox"
                                        checked={
                                            (shortMenuType_Redux)
                                                ? true
                                                : false
                                        }

                                        // делаем его недоступным для нажатия мыши, посколько оно перехватывает Клик по блоку, и не срабатывает фугкционал. Нажатие реализовано через клик по всему блоку.
                                        style={{ pointerEvents: "none" }}
                                        readOnly={true}
                                    />
                                </div>

                                <div className="optionText"
                                    style={{
                                        marginLeft: "2px",
                                    }}
                                >
                                    {/*    Языковой блок    */}
                                    {(languageChoosed_Redux == "de") ? "Dropdown-Schaltfläche" : null}
                                    {(languageChoosed_Redux == "pl") ? "Przycisk rozwijany" : null}
                                    {(languageChoosed_Redux == "ua") ? "Кнопка, що розкривається" : null}
                                    {(languageChoosed_Redux == "ru") ? "Раскрывающаяся кнопка" : null}
                                    {(languageChoosed_Redux == "en") ? "Drop-down button" : null}
                                </div>
                            </div>

                            {/*    Это кнопка "Развернуть меню"    */}
                            <div className="menuSubContent_Button"
                                onClickCapture={() => {
                                    BisGlobFunctions.setShortMenuType(false);
                                }}
                            >
                                <div className="check_Block">
                                    <input
                                        type="checkbox"
                                        checked={
                                            (shortMenuType_Redux)
                                                ? false
                                                : true
                                        }
                                        // делаем его недоступным для нажатия мыши, посколько оно перехватывает Клик по блоку, и не срабатывает фугкционал. Нажатие реализовано через клик по всему блоку.
                                        style={{ pointerEvents: "none" }}
                                        readOnly={true}
                                    />
                                </div>

                                <div className="optionText"
                                    style={{
                                        marginLeft: "2px",
                                    }}
                                >
                                    {/*    Языковой блок    */}
                                    {(languageChoosed_Redux == "de") ? "Im Titel erweitert" : null}
                                    {(languageChoosed_Redux == "pl") ? "Rozszerzony tytuł" : null}
                                    {(languageChoosed_Redux == "ua") ? "Розгорнуто в заголовку" : null}
                                    {(languageChoosed_Redux == "ru") ? "Развернуто в заголовке" : null}
                                    {(languageChoosed_Redux == "en") ? "Expanded in title" : null}
                                </div>
                            </div>

                        </div>

                    </div>



                    <div className="menuContentBlock_TextOnly"
                        style={{
                            borderTopStyle: "solid",
                            borderTopWidth: "1px",
                            marginTop: "10px",
                            paddingTop: "10px",
                            // marginLeft: "10px",
                        }}
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Sprache:" : null}
                        {(languageChoosed_Redux == "pl") ? "Język:" : null}
                        {(languageChoosed_Redux == "ua") ? "Мова:" : null}
                        {(languageChoosed_Redux == "ru") ? "Язык:" : null}
                        {(languageChoosed_Redux == "en") ? "Language:" : null}
                    </div>

                    {/*     Это список языков     */}
                    <div className="menuSubContentBlock"
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            marginTop: "10px",
                            marginBottom: "10px",
                            paddingLeft: "20px",
                            paddingBottom: "10px",

                            borderBottomStyle: "solid",
                            borderBottomWidth: "1px",
                        }}

                    >
                        {/*    Тут выводим список языков    */}
                        {
                            GLOB_DATA.languagesVariantsList.map(
                                (item, index) => {
                                    return (
                                        <div className="menuSubContent_Button"
                                            key={index}
                                            style={{ margin: "0px" }}
                                            onClick={() => {
                                                BisGlobFunctions.setlanguageChoosed(item.returnlanguageValue);
                                            }}
                                        >
                                            <div className="chooseLanguageBlock">
                                                <div className="chooseLanguageBlock_Left">

                                                    <div className="check_Block">
                                                        <input
                                                            type="checkbox"
                                                            checked={
                                                                (item.returnlanguageValue == languageChoosed_Redux)
                                                                    ? true
                                                                    : false
                                                            }
                                                            // делаем его недоступным для нажатия мыши, посколько оно перехватывает Клик по блоку, и не срабатывает фугкционал. Нажатие реализовано через клик по всему блоку.
                                                            style={{ pointerEvents: "none" }}

                                                            readOnly={true}
                                                        />
                                                    </div>

                                                    <div className="flagBlock"
                                                        style={{
                                                            display: "flex",
                                                            flexDirection: "row",
                                                            alignItems: "flex-end", // нужно, иначе маркер справа смещается вверх
                                                            height: "16px",
                                                            width: "22px",
                                                            marginLeft: "20px",
                                                        }}
                                                    >
                                                        <img src={item.flagImageAdress} height={"100%"} width={"100%"} />
                                                    </div>


                                                    <div className="languageNAME"
                                                        style={{
                                                            marginLeft: "10px",
                                                        }}
                                                    >
                                                        {item.visiblelanguageText}
                                                    </div>
                                                </div>

                                            </div>
                                        </div>
                                    )
                                })
                        }
                    </div>

                    {/*     Это кнопка справки     */}
                    <div className="menuSubContent_Button"
                        style={{
                            justifyContent: "space-between",
                        }}
                        onClickCapture={() => {
                            my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                            my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialogFormIs_aboutSoft" });
                        }}
                    >
                        <div className="left">
                            {/*    Языковой блок    */}
                            {(languageChoosed_Redux == "de") ? "Über Software ..." : null}
                            {(languageChoosed_Redux == "pl") ? "O oprogramowaniu ..." : null}
                            {(languageChoosed_Redux == "ua") ? "Про додаток ..." : null}
                            {(languageChoosed_Redux == "ru") ? "О программе ..." : null}
                            {(languageChoosed_Redux == "en") ? "Аbout soft ..." : null}
                        </div>

                        <div className="right">
                            <div className="roundBbutton"
                                title="Color theam"
                                style={{
                                    fontSize: "1.2em",
                                    fontWeight: "bold",
                                    marginLeft: "20px",
                                }}
                            >
                                <div className="roundBbutton svg__btnSymbol" >
                                    <m_SVG_ICONS.aboutServise className="mSvgFile" />
                                </div>
                            </div>
                        </div>
                    </div>


                    {/*     НЕ УДАЛЯТЬ - доп функции для Глоб Админа    */}
                   
                    {
                        // доп функции для Глоб Админа
                        mConfig.globAdminIdentifications.has(currentUser_Redux)
                            ?
                            <div
                                style={{
                                    flexDirection: "column",
                                    backgroundColor: "gray",
                                    marginTop: "40px",
                                }}
                            >
                                ____________________ <br />

                                <div className="flex_column">

                                    <div
                                        onClickCapture={() => {
                                            const reqPsevdo = {
                                                endPoint: "get_full_data_from_server",
                                                postDataToServer: {
                                                    // user_Email: currentUser, // по факту не используется в этом запросе
                                                },
                                            }
                                            psevdoServer(reqPsevdo);
                                        }}
                                    >
                                        m_get_full_data_from_server to console
                                    </div>

                                    <div
                                        onClickCapture={() => {
                                            console.log("ЛОКАЛЬНЫЕ ДАННЫЕ:");
                                            console.log("bisDataRedux= ");
                                            console.log(bisDataRedux);
                                            console.log("chatListRedux= ");
                                            console.log(chatListRedux);
                                        }}
                                    >
                                        output all local data to console
                                    </div>

                                    <div
                                        onClickCapture={() => {
                                            console.log("ЛОКАЛЬНЫЕ ДАННЫЕ - GLOB_DATA=");
                                            console.log(GLOB_DATA);
                                        }}
                                    >
                                        output GLOB_DATA
                                    </div>

                                </div>

                            </div>
                            : null
                    }



                </div >

            </div >

        </div >
    )
}

