import React from 'react';
import { useDispatch, useSelector } from "react-redux";
// import { myTD_BS, saveLocalStorage, loadLocalStorage } from '../privateFormsAndFunctions/m_private_functions.js';
// import Header from "../header/header";
// import { PrivateListMenu, CurrentPrivateList } from '../privateFormsAndFunctions/m_private_forms';
import { BisMenu } from '../bisFormsAndFunctions/bis_menu.jsx';
import { BisSubContent } from '../bisFormsAndFunctions/bis_GlobContenner.jsx';
import DialogForm_Fone_LEVEL1 from './dialogForm.jsx';


function KontentBlock() {
    const my_dispatch = useDispatch();
    const mediaAdaptationData_mobilAdaptationName_FromRedux = useSelector(mState => mState.reduser_mediaAdaptionData.mobilAdaptationName);

    return (
        <div className='KontentBlock'
            style={{
                marginTop: "5px",
                overflow: "hidden",
                flex: 1,
            }}
        >
            <div className='content_LEFT'
                // медиаадаптация
                style={
                    (mediaAdaptationData_mobilAdaptationName_FromRedux == "mobilAdaptationName_01")
                        ?
                        {
                            width: "100%",
                            maxWidth: "600px",
                            // marginRight: "10px"
                        }
                        :
                        {
                            display: "flex",
                            flexDirection: "row",
                            justifyContent: "flex-start",
                            width: "300px",
                            minWidth: "300px",
                            height: "100%",
                        }
                }
            >
                <BisMenu />
            </div>

            <div className="content_RIGHT"
                // медиаадаптация
                style={
                    (mediaAdaptationData_mobilAdaptationName_FromRedux == "mobilAdaptationName_01") ?
                        { display: "none" }
                        :
                        {
                            display: "flex",
                            flexDirection: "row",
                            flexWrap: "nowrap",
                            width: "100%",
                            height: "100%",
                            overflow: "hidden",
                            marginLeft: "40px",
                        }
                }
            >
                < BisSubContent />
            </div>



        </div >
    )
}

//--------------------
export default KontentBlock;


