export const reduser_colorSheme = (
    currentState = 2,
    my_action) => {
    switch (my_action.type) {
        case "set_colorShemeCurrentNumber": {
            GLOB_DATA.currentColorSheme_readOnly = my_action.new_colorShemeNumber;
            return (my_action.new_colorShemeNumber);

            break;
        }

        default: return currentState;
    }
}

