const default_ActiveProjectAndSubproject = {
    currentActiveProject_INDEX: -1,
    currentActiveProject_ID: null,
    currentActiveSubProject_INDEX: -1,
    currentActiveSubProject_ID: null,
}

export const reduser_ActiveProjectAndSubproject = (
    currentState = default_ActiveProjectAndSubproject,
    my_action) => {

    switch (my_action.type) {

        case "SET_currentActiveProject_ID": {
            GLOB_DATA.activeProject_ID_FromRedux_readOnly = my_action.new_currentActiveProject_ID;

            let calculateActiveProject_INDEX = BisGlobFunctions.find_Project_index_in_BD(my_action.new_currentActiveProject_ID);

            GLOB_DATA.active_project_INDEX_FromRedux_readOnly = calculateActiveProject_INDEX;

            return {
                ...currentState,
                currentActiveProject_ID: my_action.new_currentActiveProject_ID,
                // также и определяем и записываем активный индекс проекта в общей базе данных
                currentActiveProject_INDEX: calculateActiveProject_INDEX,
            }
            break;
        }

        case "SET_currentActiveSubproject_INDEX": {
            GLOB_DATA.active_subProject_INDEX_FromRedux_readOnly = my_action.new_currentActiveSubproject_INDEX;
            return {
                ...currentState,
                currentActiveSubProject_INDEX: my_action.new_currentActiveSubproject_INDEX
            }
            break;
        }

        case "SET_currentActiveSubproject_ID": {
            GLOB_DATA.activeSubProject_ID_readOnly = my_action.new_currentActiveSubproject_ID;
            GLOB_DATA.active_subProject_INDEX_FromRedux_readOnly = BisGlobFunctions.find_subProject_index_in_BD(
                currentState.currentActiveProject_INDEX, // берем из текущего состояния
                my_action.new_currentActiveSubproject_ID,
            );

            return {
                ...currentState,
                currentActiveSubProject_ID: my_action.new_currentActiveSubproject_ID,
                // также и определяем и записываем активный индекс проекта в общей базе данных

                currentActiveSubProject_INDEX: BisGlobFunctions.find_subProject_index_in_BD(
                    currentState.currentActiveProject_INDEX, // берем из текущего состояния
                    my_action.new_currentActiveSubproject_ID,
                ),

            }
            break;
        }

        default: return currentState;
    }
}

