export const reduser_bisData = (
    currentState = null,
    my_action) => {
    switch (my_action.type) {
        case "UPDATEData": {
            // console.log("=== СРАБОТАЛ reduser_bisData");
            return {
                ...currentState,
                ...my_action.new_bisData,
            }
            break;
        }

        default: return currentState;
    }
}

