const default_filtersAndSortSettings = {

    projectsFilters: {}, // допустимые значения: null   "noComplete"   "complete"
    projectsSorts: {}, // допустимые значения: null    "beginNew"    "byName___A_Z"     "byName___Z_A"     "byDedline"

    subProjectsFilters: {}, // допустимые значения: null   "vsMyParticipation"   "noComplete"   "complete"
    subProjectsdSorts: {}, // допустимые значения: null    "beginNew"    "byName___A_Z"     "byName___Z_A"     "byDedline"
}




export const reduser_filtersAndSortSettings = (
    currentState = default_filtersAndSortSettings,
    my_action) => {
    switch (my_action.type) {
        case "SET_filtersAndSortSettings": {

            GLOB_DATA.filtersAndSort_settings = my_action.new_filtersAndSortSettings;

            // console.log("GLOB_DATA.filtersAndSort_settings ");
            // console.log(GLOB_DATA.filtersAndSort_settings);

            return {
                // тут копируем структуру данных
                ...currentState,
                // тут разворачиваем обновленные данные
                ...my_action.new_filtersAndSortSettings
            }
            break;
        }

        default: return currentState;
    }

}

