const all_redusers = combineReducers(
    {
        // редюсеры можно передавать просто списком, а можно структурой "ключ: значение"
        reduser_colorSheme: reduser_colorSheme,
        reduser_soundOnStatus: reduser_soundOnStatus,
        reduser_languageChoosed: reduser_languageChoosed,
        reduser_shortMenuType: reduser_shortMenuType,

        reduser_DialogForm: reduser_DialogForm,
        reduser_currentUser: reduser_currentUser,
        reduser_accessToken: reduser_accessToken,

        // corporative Data:
        reduser_corpAccount: reduser_corpAccount,
        reduser_corpList_indication: reduser_corpList_indication,
        reduser_corpIndication: reduser_corpIndication,

        reduser_ActiveProjectAndSubproject: reduser_ActiveProjectAndSubproject,
        // reduser_userReestrDataRedux: reduser_userReestrDataRedux,
        reduser_bisData: reduser_bisData,
        reduser_usersOnlineStatus: reduser_usersOnlineStatus,
        reduser_chatListRedux: reduser_chatListRedux,

        reduser_mediaAdaptionData: reduser_mediaAdaptionData,
        reduser_filtersAndSortSettings: reduser_filtersAndSortSettings,


    }
)

