    function SubDialog_view_filesOfProject() {

        const bisDataRedux = useSelector(mState => mState.reduser_bisData);

        const [isSelectedFiles, set_isSelectedFiles] = useState(false);
        const [editableFilesListCurrentProject, set_editableFilesListCurrentProject] = useState([]);

        const ref_inputFile = useRef();

        useEffect(() => {
            if (active_project_INDEX_FromRedux >= 0) {
                // editableFalesListCurrentProject = bisDataRedux.projects[active_project_INDEX_FromRedux].attachedFiles.filesList;
                // отключаем чекбоксы при открытии формы (они могут быть установлены при пред использовании)
                bisDataRedux.projects[active_project_INDEX_FromRedux].attachedFiles.filesList.forEach(item => {
                    if (item.isChecked) item.isChecked = false;
                });

                set_editableFilesListCurrentProject(bisDataRedux.projects[active_project_INDEX_FromRedux].attachedFiles.filesList);
                console.log("editableFilesListCurrentProject= ");
                console.log(editableFilesListCurrentProject);
            }
        }, []
        )

        return (
            <div
                className="subDialogForm_styleType01"
                className={
                    (mediaAdaptationData_mobilAdaptationName_FromRedux == "mobilAdaptationName_01")
                        ? "subDialogForm_styleType01 additional_Form_mobile_01"
                        : "subDialogForm_styleType01"
                }
            >

                <div className="topOfDialogForm">
                    <div className="formHeaderName">
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Projektdateien:" : null}
                        {(languageChoosed_Redux == "pl") ? "Pliki projektu:" : null}
                        {(languageChoosed_Redux == "ua") ? "Файли проекту:" : null}
                        {(languageChoosed_Redux == "ru") ? "Файлы проекта:" : null}
                        {(languageChoosed_Redux == "en") ? "Project files:" : null}
                    </div>

                    <div className="closeFormBlock"
                        onClickCapture={() => GLOB_DATA.closeDialogForm()}
                    >
                        X {/* заменить на виджет */}
                    </div>
                </div>

                <div className="centerOfDialogForm">

                    <div className="smallTextKontent_type01"> {bisDataRedux.projects[active_project_INDEX_FromRedux].projectSettings.project_Name}  </div>

                    <div className="tempBlock"
                        style={{
                            // background: "gray",
                            width: "100%",
                            minHeight: "150px",
                        }}
                    >

                        <div className="filesContenner"
                            style={{
                                flexDirection: "column",
                                justifyContent: "space-between",
                                width: "46px"
                            }}
                        >
                            <div className="filesContenner_header">

                                <div className="smallTextKontent_type01">
                                    {(languageChoosed_Redux == "de") ? "Dateien:" : null}
                                    {(languageChoosed_Redux == "pl") ? "Pliki:" : null}
                                    {(languageChoosed_Redux == "ua") ? "Файли:" : null}
                                    {(languageChoosed_Redux == "ru") ? "Файлы:" : null}
                                    {(languageChoosed_Redux == "en") ? "Files:" : null}
                                </div>

                            </div>

                            <div className="filesContenner_content"
                                style={{
                                    flexDirection: "column",
                                    justifyContent: "flex-end",
                                    alignItems: "center",
                                    height: "100%",
                                }}
                            >
                                . . .
                                <div className="btnContenner"
                                    style={{
                                        flexDirection: "column",
                                    }}
                                >

                                    <button className={
                                        (active_Project_ID_FromRedux)
                                            ? "mBtn_01"
                                            : "mBtn_01 btnDisabled"
                                    }
                                        style={{ marginTop: "4px" }}
                                        tabIndex={-1} // чтобы при неактивном классе btnDisabled было невозможно перейти на кнопку через клавишу TAB
                                        title="Просмотр файлов"
                                        onClickCapture={() => {
                                            my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                                            my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialogFormIs_view_filesOfProject" });
                                        }}
                                    >
                                        <div className="svg__btnSymbol" >
                                            <m_SVG_ICONS.lookFiles className="mSvgFile" />
                                        </div>

                                    </button>

                                    <button className={
                                        (active_Project_ID_FromRedux)
                                            ? "mBtn_01"
                                            : "mBtn_01 btnDisabled"
                                    }
                                        style={{ marginTop: "4px" }}
                                        tabIndex={-1} // чтобы при неактивном классе btnDisabled было невозможно перейти на кнопку через клавишу TAB
                                        title="Добавить файл"
                                        onClickCapture={() => {
                                            my_dispatch({ type: "SET_isDialogVisible", new_isDialogVisible: true });
                                            my_dispatch({ type: "SET_carrentActiveSubdialog", new_carrentActiveSubdialog: "currentDialogFormIs_uploadFile_SettingsForm" });
                                        }}
                                    >
                                        <div className="svg__btnSymbol" >
                                            <m_SVG_ICONS.addFiles className="mSvgFile" />
                                        </div>

                                    </button>
                                </div>

                            </div>

                        </div>


                    </div>

                    <div className="outputFalesOfProjectList">
                        {
                            editableFilesListCurrentProject
                                ?
                                editableFilesListCurrentProject.map(
                                    (item, index) => (
                                        <div className="singleFileInList" key={BisGlobFunctions.myRandomId()}>

                                            <div className="singleFileInList_Left">

                                                <div className="singleFileInList_Left_checkBoxBlock">
                                                    <input
                                                        type="checkbox"
                                                        style={{
                                                            cursor: "default",
                                                        }}
                                                        defaultChecked={
                                                            (item && item.isChecked)
                                                                ? true
                                                                : false
                                                        }
                                                        onChangeCapture={(e) => {
                                                            item.isChecked = e.target.checked; // это действие записывает состояние отображаемого чекбокса в состояние переменной editableFilesListCurrentProject, НО НЕ ВЫЗЫВАЕТ ПЕРЕРЕНДЕРИНГ всего окна, поскольку мы не вызываем обновление стейта через функцию set_editableFilesListCurrentProject.
                                                            console.log("editableFilesListCurrentProject=");
                                                            console.log(editableFilesListCurrentProject);

                                                            // отмечаем наличие/отсутствие отмеченных файлов
                                                            let findCheckedIndex = editableFilesListCurrentProject.findIndex(item => item.isChecked === true);
                                                            if (findCheckedIndex != null && findCheckedIndex >= 0) set_isSelectedFiles(true);
                                                            else set_isSelectedFiles(false);
                                                        }}
                                                    />
                                                </div>

                                                <div className="singleFileInList_Left_fileContentBlock">

                                                    <div className="fileContentBlock_TOP">
                                                        <div className="fileName"> {item.file_name} </div>
                                                    </div>

                                                    <div className="fileContentBlock_BOTTOM">
                                                        <div className="file_info smallTextKontent_type01">
                                                            {item.file_size} KB {" - загружено "}
                                                            {
                                                                new Date(item.dateUpLoad).toLocaleDateString() + "-" + new Date(item.dateUpLoad).toLocaleTimeString()
                                                            }
                                                        </div>

                                                        <div className="senderFile smallTextKontent_type01"> {"Отправитель: "} - {item.senderFile} </div>
                                                    </div>

                                                </div>

                                            </div>

                                            <div className="singleFileInList_Right">

                                                <div className="downLoadBlock">
                                                    \/
                                                </div>

                                                <div className="deleteBtnBlock"
                                                    title={
                                                        //   Языковой блок   
                                                        (languageChoosed_Redux == "de" ? "Löschen" : "") +
                                                        (languageChoosed_Redux == "pl" ? "Usuwać" : "") +
                                                        (languageChoosed_Redux == "ua" ? "Видалити" : "") +
                                                        (languageChoosed_Redux == "ru" ? "Удалить" : "") +
                                                        (languageChoosed_Redux == "en" ? "Delete" : "")
                                                    }
                                                    onClick={async () => {
                                                        // удаление одного файла
                                                        // отправляем запрос на сервер
                                                        const reqPsevdo = {
                                                            endPoint: "deleteFilesFromServer",
                                                            postDataToServer: {
                                                                admin_ID: currentUser_Redux,
                                                                project_ID: active_Project_ID_FromRedux,
                                                                deleteFilesList: [
                                                                    { file_ID: item.file_ID }
                                                                ],
                                                            },
                                                        }
                                                        let promiseAnswerData = await psevdoServer(reqPsevdo);

                                                        console.log("После удаления файлов, новый fileList= ");
                                                        console.log(promiseAnswerData);

                                                        // обновляем локальное состояние новым списком файлов
                                                        set_editableFilesListCurrentProject(promiseAnswerData.newFilesList);
                                                        // обновляем редакс БЕЗ ОБНОВЛЕНИЯ СТЕЙТА
                                                        bisDataRedux.projects[active_project_INDEX_FromRedux].attachedFiles.filesList = promiseAnswerData.newFilesList;
                                                    }}
                                                >
                                                    X
                                                </div>

                                            </div>

                                        </div>
                                    )
                                )
                                : ""
                        }
                    </div>

                    <div className="buttonsBlock_subBlockVertical">
                        Действия с выбранными файлами:
                    </div>

                    <div className="buttonsBlock_subBlockVertical">

                        <button className="mBtn_01"
                            disabled={!isSelectedFiles}
                            onClickCapture={async () => {
                                // подготавливаем список файлов для удаления
                                let downloadFalesList = editableFilesListCurrentProject.filter(item => item.isChecked === true);
                                // отбираем нужные данные
                                /* 
                                downloadFalesList = downloadFalesList.map(item => {
                                    return ({ file_ID: item.file_ID });
                                });
                                 */

                                console.log("downloadFalesList=");
                                console.log(downloadFalesList);

                                // отправляем запрос на сервер
                                downloadFalesList.forEach(async (item) => {
                                    const reqPsevdo = {
                                        endPoint: "downloadOneFileFromServer",
                                        postDataToServer: {
                                            admin_ID: currentUser_Redux,
                                            project_ID: active_Project_ID_FromRedux,
                                            file_ID: item.file_ID,
                                        },
                                    }
                                    let promiseAnswerData = await psevdoServer(reqPsevdo);
                                    console.log("После загрузки файла promiseAnswerData= ");
                                    console.log(promiseAnswerData);

                                    console.log("promiseAnswerData.status= " + promiseAnswerData.status);
                                    if (promiseAnswerData.status == 200) {
                                        // тут прочитываем вложенный файл:
                                        let mBlob = await promiseAnswerData.blob();
                                        console.log("==================  mBlob= ");
                                        console.log(mBlob);
                                        // далее извлекаем файл в бинарном виде и сохраняем его, см. инструкцию на 6 минуте: 
                                        // https://www.youtube.com/watch?v=LHFRwvnrgIU&list=PL6DxKON1uLOGd4E6kG6d5K-tsTFj-Deln&index=17&ab_channel=UlbiTV
                                        // тут извлекаем из респонса файл в бинарном виде
                                        let mDownloadUrl = window.URL.createObjectURL(mBlob);
                                        console.log("==================  mDownloadUrl= ");
                                        console.log(mDownloadUrl);
                                        // создаем невидимую ссылку, ОБЯЗАТЕЛЬНО используем тег "a"
                                        let mLink = document.createElement("a");  //ОБЯЗАТЕЛЬНО используем тег "a"
                                        mLink.href = mDownloadUrl;
                                        mLink.download = item.file_name;
                                        document.body.appendChild(mLink);
                                        console.log("==================  mLink= ");
                                        console.log(mLink);
                                        mLink.click(); // имитируем нажатие пользователя на ссылку
                                        mLink.remove(); // удаляем ссылку после использования
                                    }
                                })
                            }}
                        >
                            Скачать
                        </button>

                        <button className="mBtn_01"
                            disabled={!isSelectedFiles}
                            onClickCapture={async () => {
                                // подготавливаем список файлов для удаления
                                let deleteFalesList = editableFilesListCurrentProject.filter(item => item.isChecked === true);
                                // отбираем нужные данные
                                deleteFalesList = deleteFalesList.map(item => {
                                    return ({ file_ID: item.file_ID });
                                });

                                console.log("deleteFalesList=");
                                console.log(deleteFalesList);

                                // отправляем запрос на сервер
                                const reqPsevdo = {
                                    endPoint: "deleteFilesFromServer",
                                    postDataToServer: {
                                        admin_ID: currentUser_Redux,
                                        project_ID: active_Project_ID_FromRedux,
                                        deleteFilesList: deleteFalesList,
                                    },
                                }
                                let promiseAnswerData = await psevdoServer(reqPsevdo);

                                console.log("После удаления файлов, новый fileList= ");
                                console.log(promiseAnswerData);

                                // обновляем локальное состояние новым списком файлов
                                set_editableFilesListCurrentProject(promiseAnswerData.newFilesList);

                            }}
                        >
                            Удалить
                        </button>

                        <div className="buttonsBlock_subBlockVertical">
                            <button className="mBtn_01"
                                disabled={!isSelectedFiles}
                                onClickCapture={() => {
                                    // set_editableFilesListCurrentProject(null);
                                    set_isSelectedFiles(false);
                                    ref_inputFile.current.value = null; // очистить выбранные файлы
                                }}
                            >
                                Добавить файлы в проект
                            </button>

                        </div>

                    </div>

                </div>



                <div className="bottomOfDialogForm" >

                    <button className="mBtn_01"
                        onClickCapture={() => {
                            // set_editableFilesListCurrentProject(null);
                            GLOB_DATA.closeDialogForm();
                        }}
                    >
                        Закрыть
                    </button>

                </div>

            </div >
        )
        //---------------
        function SingleFileInList(props) {

            return (
                <div className="SingleFileItem">

                </div>
            )
        }

    }
