    function SubDialog_uploadFiles() {

        const bisDataRedux = useSelector(mState => mState.reduser_bisData);
        // const [wasChangeInSettings, set_wasChangeInSettings] = useState(false);
        const [filesListForUpload, set_filesListForUpload] = useState([]);
        const ref_inputFile = useRef();

        return (
            <div
                className="subDialogForm_styleType01"
                className={
                    (mediaAdaptationData_mobilAdaptationName_FromRedux == "mobilAdaptationName_01")
                        ? "subDialogForm_styleType01 additional_Form_mobile_01"
                        : "subDialogForm_styleType01"
                }
            >

                <div className="topOfDialogForm">
                    <div className="formHeaderName">
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Dateien zu einem Projekt hinzufügen" : null}
                        {(languageChoosed_Redux == "pl") ? "Dodawanie plików do projektu" : null}
                        {(languageChoosed_Redux == "ua") ? "Додавання файлів до проекту" : null}
                        {(languageChoosed_Redux == "ru") ? "Добавление файлов к проекту: " : null}
                        {(languageChoosed_Redux == "en") ? "Adding Files to a Project: " : null}
                    </div>

                    <div className="closeFormBlock"
                        onClickCapture={() => GLOB_DATA.closeDialogForm()}
                    >
                        X {/* заменить на виджет */}
                    </div>
                </div>



                <div className="centerOfDialogForm">




                    <div>
                        {bisDataRedux.projects[active_project_INDEX_FromRedux].projectSettings.project_Name}
                    </div>


                    <input
                        type="file"
                        id="if_inputFile"
                        ref={ref_inputFile}
                        style={{ display: "none" }}
                        multiple
                        onChange={(e) => {
                            console.log("ВЫБРАННЫЕ ФАЙЛЫ e.target.files=:");
                            console.log(e.target.files);
                            set_filesListForUpload(e.target.files);
                            console.log("filesListForUpload.length= " + filesListForUpload.length);
                            // if (filesListForUpload.length > 0) set_wasChangeInSettings(true);
                            // else set_wasChangeInSettings(false);
                        }}
                    />

                    {/*    это стилизованная кнопка для input    */}
                    <label className="buttonLabel_FOR_inputFile mBtn_01"
                        htmlFor="if_inputFile"
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Fehler – Falsches Passwort eingegeben." : null}
                        {(languageChoosed_Redux == "pl") ? "Wybierz plik" : null}
                        {(languageChoosed_Redux == "ua") ? "Вибрати файл" : null}
                        {(languageChoosed_Redux == "ru") ? "Выбрать файл" : null}
                        {(languageChoosed_Redux == "en") ? "Select a file" : null}

                    </label>

                    <br />

                    <div className="dragField"

                        onDragOver={(e) => {
                            e.preventDefault(); // предотвращение деяствия по умолчанию для элемента HTML, в т.ч. предотвращение отправки формы и последующей перезагрузки окна после отправки формы
                            e.stopPropagation(); // предотвращение всплытия события вверх
                            // console.log("Сработал onDragOver");
                            // this.style = { margibackgroundColornLeft: "greenyellow" }
                        }}

                        onDragEnter={(e) => {
                            console.log("Сработал onDragEnter");
                            e.preventDefault(); // предотвращение деяствия по умолчанию для элемента HTML, в т.ч. предотвращение отправки формы и последующей перезагрузки окна после отправки формы
                            e.stopPropagation(); // предотвращение всплытия события вверх
                        }}


                        onDrop={(e) => {
                            console.log("Сработал onDrop");
                            e.preventDefault(); // предотвращение деяствия по умолчанию для элемента HTML, в т.ч. предотвращение отправки формы и последующей перезагрузки окна после отправки формы
                            e.stopPropagation(); // предотвращение всплытия события вверх

                            console.log("ПЕРЕТАСКАННЫЕ ФАЙЛЫ e.dataTransfer.files=:");
                            console.log(e.dataTransfer.files);

                            set_filesListForUpload(e.dataTransfer.files);
                            console.log("filesListForUpload= ");
                            console.log(filesListForUpload);
                            console.log("filesListForUpload.length= " + filesListForUpload.length);
                            // if (filesListForUpload.length > 0) set_wasChangeInSettings(true);
                            // else set_wasChangeInSettings(false);
                        }}

                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Oder Dateien hierher ziehen..." : null}
                        {(languageChoosed_Redux == "pl") ? "Lub przeciągnij pliki tutaj..." : null}
                        {(languageChoosed_Redux == "ua") ? "Або перетягніть сюди файли ..." : null}
                        {(languageChoosed_Redux == "ru") ? "Либо перетащите сюда файлы ..." : null}
                        {(languageChoosed_Redux == "en") ? "Or drag files here..." : null}
                    </div>

                    <div className="uploadfilesListForUpload">
                        <OutputFileList />
                    </div>
                </div>




                <div className="bottomOfDialogForm" >

                    <button className="mBtn_01"
                        disabled={!(filesListForUpload.length > 0)}

                        onClickCapture={async () => {
                            console.log("Начинаем загрузку файлов");
                            // преобразовываем "filesListForUpload" в объект, подобный тому, который отправляетися c PostMan, объект с файлами  преобразовываем в массив файлов
                            let modification_filesListForUpload = [...filesListForUpload];
                            // файлы отправляем на сервер поочередно, дожидаясь загрузки каждого предыдущего файла
                            let i = 0;
                            async function uploadSteckFiles_StepByStep(i) {
                                // удалить задержку setTimeout после тестирования
                                setTimeout(async () => {
                                    console.log("Загружаем файл №" + (i + 1));
                                    const myFormData = new FormData();
                                    myFormData.append('m_oneFile', modification_filesListForUpload[i])
                                    // добавляем дополнит данный в объект myFormData, при этом на сервере они будут находиться в "body"
                                    myFormData.append('user_Email', currentUser_Redux);
                                    myFormData.append('project_ID', active_Project_ID_FromRedux);
                                    // ВАЖНО УЧИТЫВАТЬ СЛЕД: на сервере наши переменные "user_Email" и "project_ID" - оказываются в теле запроса "body", а объект "files" - находится не в "body", а непосредственно в объекте "req". 
                                    // отправляяем на сервер
                                    let reqPsevdo = {
                                        endPoint: "uploadOneFileToServer",
                                        myFormData: myFormData,
                                    }

                                    let promiseAnswerData = await psevdoServer(reqPsevdo)
                                        .then((res) => {
                                            console.log("После добавления ОДНОГО файлов, новый fileList= ");
                                            console.log(res);
                                            // с пом переменной "i" мы имитируем FOR-NEXT, но в контролируемом асинхронном режиме. Чтобы след файл начинал загрузку после завершения предыдущего
                                            i++;
                                            if (!modification_filesListForUpload || (i >= modification_filesListForUpload.length)) return;
                                            uploadSteckFiles_StepByStep(i);
                                        })
                                    // обновляем локальное состояние новым списком файлов
                                    // set_editablefilesListForUploadCurrentProject(promiseAnswerData.newfilesListForUpload);

                                }, 0);

                            }
                            uploadSteckFiles_StepByStep(i);

                        }}
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Hinzufügen" : null}
                        {(languageChoosed_Redux == "pl") ? "Dodać" : null}
                        {(languageChoosed_Redux == "ua") ? "Додати" : null}
                        {(languageChoosed_Redux == "ru") ? "Добавить" : null}
                        {(languageChoosed_Redux == "en") ? "Add" : null}
                    </button>

                    <button className="mBtn_01"
                        disabled={!(filesListForUpload.length > 0)}
                        onClickCapture={() => {
                            set_filesListForUpload([]);
                            // set_wasChangeInSettings(false);
                            ref_inputFile.current.value = null; // очистить выбранные файлы
                        }}
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Zurücksetzen" : null}
                        {(languageChoosed_Redux == "pl") ? "Resetowanie" : null}
                        {(languageChoosed_Redux == "ua") ? "Скинути" : null}
                        {(languageChoosed_Redux == "ru") ? "Сбросить" : null}
                        {(languageChoosed_Redux == "en") ? "Reset" : null}
                    </button>

                    <button className="mBtn_01"
                        onClickCapture={() => {
                            set_filesListForUpload([]);
                            GLOB_DATA.closeDialogForm();
                        }}
                    >
                        {/*    Языковой блок    */}
                        {(languageChoosed_Redux == "de") ? "Stornieren" : null}
                        {(languageChoosed_Redux == "pl") ? "Anulować" : null}
                        {(languageChoosed_Redux == "ua") ? "Скасувати" : null}
                        {(languageChoosed_Redux == "ru") ? "Отмена" : null}
                        {(languageChoosed_Redux == "en") ? "Cancel" : null}
                    </button>

                </div>


            </div >
        )
        //---------------
        function OutputFileList() {

            let modification_filesListForUpload = Array.from(filesListForUpload);
            return (
                <div className="OutputFileList">
                    {
                        (modification_filesListForUpload && (modification_filesListForUpload.length > 0))
                            ?
                            modification_filesListForUpload.map(
                                (item, index) => (
                                    <div className="fileItem" key={BisGlobFunctions.myRandomId()}  >
                                        {item.name}
                                    </div>
                                )
                            )
                            : ""
                    }
                </div >
            )
        }
    }
