import { GLOB_DATA } from "../GLOB_DATA.js";

//==========================

class mLocalStorage {
    loadLocalStorageBis() {
        console.log('=======================');
        console.log('Запуск loadLocalStorageBis :');
        console.log('=======================');
        let data = localStorage.getItem('myLocalStorageBis');
        // console.log('data=');
        // console.log(data);
        if (data) {
            GLOB_DATA.current_LocalStorData = JSON.parse(data);
        };
        // console.log('GLOB_DATA.current_LocalStorData=');
        // console.log(GLOB_DATA.current_LocalStorData);

        // console.log('ПРОЧТЕНИЕ LocalStorage, GLOB_DATA.current_LocalStorData = ');
        // console.log(GLOB_DATA.current_LocalStorData);
        // return GLOB_DATA.current_LocalStorData;
    }
    // --------------------------
    async saveLocalStorageBis() {
        console.log('=======================');
        console.log('Запуск saveLocalStorageBis :');
        console.log('=======================');
        // console.log(GLOB_DATA.current_LocalStorData);
        localStorage.setItem('myLocalStorageBis', JSON.stringify(GLOB_DATA.current_LocalStorData));
    }
    // --------------------------
    deleteLocaleStorageBis() {
        localStorage.removeItem('myLocalStorageBis');
    }
}

//==========================

export default new mLocalStorage;
